// Helpers partags serveur/client
// ===============================

'use strict';

/* global window */

// Sauf  coller underscore et moment  la racine du projet (burk)
// y'a pas trop de moyen d'avoir simplement un `require` uniforme
// entre Node et Brunch sur ce coup (mme en dplaant toute la partie
// Brunch : `config.coffee`, `package.json` et `node_modules` ddis
// dans `client`).  Donc on les inclue en mode `vendor` (pas d'enrobage
// CommonJS) et on se base sur la prsence de `window` pour caler nos
// variables locales.
var inBrowser = 'undefined' !== typeof window;
var _ = inBrowser ? window._ : require('underscore');
var moment = inBrowser ?  window.moment : require('moment');
// En mode browser, la langue FR aura t implicitement charge, comme
// le reste (underscore, moment, jQuery).
if (!inBrowser) {
  require('moment/locale/fr');
}

module.exports = function(helpers) {
  _.extend(helpers, {
    // Formatage de date/heure sur format libre, avec un format long
    // comme dfaut.  On peut aussi lui passer une entit quipe d'un
    // champ `postedAt` (bookmark, commentaire) qui sera alors utilis.
    formatDate: function formatDate(d, format) {
      d = d.postedAt || d;

      return moment(d).format(format || 'dddd D MMMM YYYY  HH:mm');
    },

    // Pluraliseur basique FR, la forme plurielle tant optionnelle
    // (si elle n'est pas fournie, on ajoutera un "s" au singulier).
    pluralize: function pluralize(count, singular, plural) {
      return count + '' + (count > 1 ? plural || singular + 's' : singular);
    }
  });
};
